<template>
  <div class="">
    <div class="article">
      <div class='banner'>
        <span class='title'>
          
        </span>
        <img src="../assets/article/banner.jpg" alt="">
      </div>
    </div>
    <ul class='article-list'>
       <li v-for="(item, index) in article">
         <img v-lazy="item.imgUrl" class='fl'>
         <div class='fl descr'>
           <span class='type'>[{{ item.type }}]</span>
            <!-- <a class='title' v-link="{ name: 'ArticleDetail', params: { articleId: index }}"> {{item.title }} </a> -->
            <router-link :to="{path: `/ArticleDetail/${index}`}"> {{item.title }} </router-link>
           <p>
             {{item.intro}}
           </p>
         </div>
       </li>
    </ul>
  </div>
</template>

<script>
var article = [{
  title: '10 ',
  type: '',
  intro: ' ... ',
  imgUrl: require('../assets/article/article-1.jpg')
}, {
  title: '',
  type: '',
  intro: ' ... ',
  imgUrl: require('../assets/article/article-2.jpg')
}, {
  title: '',
  type: '',
  intro: '... ',
  imgUrl: require('../assets/article/article-3.jpg')
}, {
  title: '',
  type: '',
  intro: '... ',
  imgUrl: require('../assets/article/article-4.jpg')
}, {
  title: '',
  type: '',
  intro: ' ?... ',
  imgUrl: require('../assets/article/article-5.jpg')
}, {
  title: '',
  type: '',
  intro: '... ',
  imgUrl: require('../assets/article/article-6.jpg')
}, {
  title: ' ',
  type: '',
  intro: '... ',
  imgUrl: require('../assets/article/article-7.jpg')
}, {
  title: '',
  type: '',
  intro: ' ... ',
  imgUrl: require('../assets/article/article-8.jpg')
}, {
  title: '',
  type: '',
  intro: ' ... ',
  imgUrl: require('../assets/article/article-9.jpg')
}, {
  title: '',
  type: '',
  intro: '?...  ',
  imgUrl: require('../assets/article/article-10.jpg')
}]
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      article: article
    }
  },
  mounted () {
    const that = this
    that.updateRoutePath('/Article')
  },
  methods: {
    ...mapActions([
      'updateRoutePath'
    ])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
  .fl{
    float: left;
  }
  .fr{
    float: right;
  }
  .banner{
    width: 100%;
    margin: 0 auto;
    position: relative;
    .title{
      position: absolute;
      bottom:0;
      left: 21%;
      color:#fff;
      font-size: 35px;
    }
    img{
      width: 100%;
    }
  }
  .article-list{
    overflow: hidden;
    width: 1200px;
    margin: 20px auto;
    img{
      width: 130px;
      height: 100px;
      margin-right: 20px;
    }
    li{
      width: 100%;
      overflow: hidden;
      margin: 25px 0;
      .descr{
        padding-top: 10px;
        text-align: left;
        width:900px;
        line-height: 25px;
        span{
          font-size: 16px;
          color:#666;
          font-weight: bold;
          &.type{
            margin-right: 10px;
          }

        }
        .title{
          font-weight: bold;
          font-size: 16px;
          cursor: pointer;
        }
      }
    }
  }
</style>



